import './App.css';
import React from 'react';
import McText from 'mctext-react';
import Tooltip from 'rc-tooltip';
import 'rc-tooltip/assets/bootstrap_white.css';
import { getTextureBase64 } from './utils/textureLoader';
import { renderColoredText } from './utils/colorCodes';

export function Slot({ id, icon, amount, lore, onClick, isSearch = false, isBlock = false }) {
  const [currentIcon, setCurrentIcon] = React.useState(icon);
  const [loadError, setLoadError] = React.useState(false);

  React.useEffect(() => {
    if (!icon) {
      setLoadError(true);
      return;
    }

    // If icon is already a data URL, use it directly
    if (icon.startsWith('data:')) {
      setCurrentIcon(icon);
      setLoadError(false);
      return;
    }

    // Try to get icon from our texture loader
    const texture = getTextureBase64(icon);
    if (texture) {
      setCurrentIcon(`data:image/png;base64,${texture}`);
      setLoadError(false);
      return;
    }

    // If no texture found, use stone as fallback
    const fallbackTexture = getTextureBase64('stone');
    setCurrentIcon(fallbackTexture ? `data:image/png;base64,${fallbackTexture}` : '');
    setLoadError(true);
  }, [icon]);

  const tooltipContent = (
    <div className="item-tooltip" style={{ whiteSpace: 'nowrap' }}>
      {renderColoredText(lore || 'Unknown Item')}
    </div>
  );

  return (
    <Tooltip
      placement="right"
      overlay={tooltipContent}
      mouseEnterDelay={0.1}
      mouseLeaveDelay={0.1}
      trigger={['hover']}
      overlayClassName="minecraft-tooltip"
      overlayStyle={{ whiteSpace: 'nowrap' }}
    >
      <div
        className={`slot ${isSearch ? 'search-slot' : ''} ${isBlock ? 'block-slot' : ''}`}
        onClick={onClick}
      >
        {currentIcon && (
          <img
            src={currentIcon}
            alt={lore || ''}
            style={{
              imageRendering: 'pixelated',
              width: '28px',
              height: '28px',
              margin: '2px'
            }}
            onError={() => {
              console.warn('Failed to load image:', icon);
              setLoadError(true);
              // Try to get the fallback texture
              const fallbackTexture = getTextureBase64('stone');
              if (fallbackTexture) {
                setCurrentIcon(`data:image/png;base64,${fallbackTexture}`);
              }
            }}
          />
        )}
        {amount > 1 && <span className="item-amount">{amount}</span>}
      </div>
    </Tooltip>
  );
}
